.error{
    width: 95%;
    min-height: 150px;
    height: auto;
    box-shadow: var(--primary-boxshadow);
    border-radius: 20px;
    background-color: var(--primary-color);
    margin: auto;
}

.error__infos{
    padding: 10px;
}

.error__nameoptions{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0px;
}

.error__name{
    color: var(--error-title);
    font-size: 20px;
}

.error__description{
    color: var(--secondary-color);
    font-size: 14px;
}

.error__option{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin: 0px 10px;
    padding: 5px 12px;
    background-color: var(--sidebar-primary-color);
    border-radius: 5px;
    border: none;
    color: var(--primary-color);
}

.error__option > svg{
    margin-right: 3px;
}

.error__image{
    width: 100%;
    margin: 15px 0px;
    object-fit: cover;
    border-radius: 10px;
}