.errors{
    width: 100%;
    height: 100vh;
    display: flex;
    overflow: hidden;
}

.container{
    width: 98%;
    height: auto;
    margin: 0px auto;
    min-height: 100vh;
}

.errors__title{
    padding: 20px 10px;
    color: var(--title-color);
}

.errors__container{
    padding: 20px 0px;
    overflow-y: auto;
    width: 80%;
    height: auto;
    min-height: 90vh;
    margin-top: 20px;
    border-radius: 30px;
}